<template>
   <div>
       <div v-if="!hidden">
           <MyHeader>æ¯ä»é¡µé¢</MyHeader>
           <div style="margin-top: 3rem">
               <van-password-input
                       :value="value"
                       info="å¯ç ä¸º 6 ä½æ°å­"
                       :focused="showKeyboard"
                       @focus="showKeyboard = true"
               />
           </div>

           <!-- æ°å­é®ç -->
           <van-number-keyboard
                   :show="showKeyboard"
                   @input="onInput"
                   @delete="onDelete"
                   @blur="showKeyboard = false"
           />
       </div>

       <div v-if="hidden" class="block">
           <MyHeader>æ¯ä»æå</MyHeader>
           <div style="height: 0.88rem;"></div>
           <i class="iconfont icon-chenggong"></i>
           <div style="margin-top: 0.3rem">æ¯ä»æå</div>
           <div class="price-color">æ¯ä»éé¢ <span>ï¿¥{{price}}</span></div>
           <div class="button">
               <button><router-link to="/indexlist" style="color:#fff;">åå»éé</router-link></button>
               <button><router-link to="/index" style="color:#fff;">è¿åé¦é¡µ</router-link></button>
           </div>
       </div>



   </div>


</template>

<script>
    import MyHeader from '../../components/my-header/list-header'
    import {payOrders} from "../../api/orders";
    import Vue from 'vue';
    import {PasswordInput, NumberKeyboard} from 'vant';
    import {SUCCESS} from "../../config/base";

    Vue.use(PasswordInput).use(NumberKeyboard);
    export default {
        name: "pay",
        data() {
            return {
                value: '',
                showKeyboard: true,
                hidden: 0,
                price:'',
            };
        },
        methods: {
            onInput(key) {
                this.value = (this.value + key).slice(0, 6);
            },
            onDelete() {
                this.value = this.value.slice(0, this.value.length - 1);
            },
            payMoney() {
                let {ordernum,price} = this.$route.query;
                payOrders('/api/orders/' + ordernum, {'status': 1}).then(res=>{
                    let data = res.data;
                    if (data.code == SUCCESS) {
                        this.hidden = 1;
                        this.price=price
                    }
                })
            }

        },
        components: {
            MyHeader
        },
        watch: {
            value() {
                if (this.value == '123456') {
                    this.payMoney();
                }

            }
        }
    }


</script>

<style scoped>
    @import url(http://at.alicdn.com/t/font_1512271_vafl57zdss.css);
    .block{
