<script>

    import Myheader from '../../components/my-header/list-header'
    import {queryOrders, payOrders} from "../../api/orders";
    import {queryAddress} from "../../api/address";
    import {SUCCESS, IMGHOSTNAME} from "../../config/base";

    export default {
        name: "orders",
        components: {
            Myheader
        },
        data() {
            return {
                imgHostName: IMGHOSTNAME,
                message: '',
                address: null,
                orders: null,
                ordersnum: 0,
            }
        },
        methods: {
            setAddress() {
                queryAddress('/api/address/24').then(res => {
                    let data = res.data;
                    if (data.code == SUCCESS && data.data) {
                        this.address = res.data.data;

                    }
                })
            },
            setOrders() {
                queryOrders('/api/orders/' + this.ordersnum).then(res => {
                    let data = res.data;
                    if (data.code == SUCCESS && res.data.data) {
                        this.orders = res.data.data;


                    }
                })
            },
            goPay() {
                payOrders('/api/orders/' + this.ordersnum, {
                    aid: this.address.aid,
                    remarks: this.orders.remarks
                }).then(res => {
                    let {price, ordernum} = this.orders;
                    if (res.data.code == 200) {
                        this.$router.push({name: 'pay', query: {ordernum, price}})
                    }

                })


            }
        },
        beforeMount() {
            this.ordersnum = this.$route.query.ordersnum;
            this.setAddress();
            this.setOrders();

        },
    }
</script>

