<script>
    import MyFooter from '../../components/my-footer/my-footer';
    import {SUCCESS, IMGHOSTNAME, HOSTNAME} from "../../config/base";
    import {addcart, reducecart} from "../../api/cart";

    import axios from 'axios';

    import Vue from 'vue';
    import {Sticky} from 'vant';

    Vue.use(Sticky);

    export default {
        name: "indexlist",
        data() {
            return {
                imgHostName: IMGHOSTNAME,
                activeKey: 0,
                cate: [],
                goods: [],
                cname: [],
                isLoading: false,
                loading: false,
                finished: false,
                current: 0,
                total: 0,
                currentCate: 0,
                limit: 7,
                container: null
            }
        },
        methods: {
            getCate() {
                axios.get(HOSTNAME + '/api/goodslist')
                    .then(res => {
                        this.cate = res.data.data;
                        this.currentCate = res.data.data[0].id;


                    })
                    .catch(error => window.console.log(error))
            },

            getGoods(id) {
                this.currentCate = id;

            },

            onRefresh() {
                setTimeout(() => {
                    this.$toast('å·æ°æå');
                    this.isLoading = false;
                    this.count++;
                }, 500);
            },
            onLoad() {
                this.current++;
                axios.get(HOSTNAME + '/api/cate', {
                    params: {
                        page: this.current,
                        limit: this.limit,
                        cid: this.currentCate,

                    }
                }).then(res => {
                    let data = res.data;
                    let total = data.total;

                    if (data.code === SUCCESS) {

                        !this.total && (this.total = Math.ceil(total / this.limit));

                        this.goods.push(...data.data)

                    }

                    this.$nextTick(() => {
                        this.isLoading = false;
                        this.loading = false;
                    });

                    if (this.current >= this.total) {
                        this.finished = true
                    }
                })
                    .catch()


            },
            addGoods(goods) {
                let {gid, mprice, gname, gthumb} = goods;
                addcart('/api/cart', {gid: gid, price: mprice}).then(res => {
                    let data = res.data;
                    if (data.code == SUCCESS) {
                        let {uid, cid} = data.data;
                        //ä¿®æ¹vuex cart
                        this.$store.commit('incGoods', {cid, gid, uid, mprice, gname, gthumb});
                        this.$store.commit('calcTotal');
                        this.$store.commit('calcPrice');

                    }
                });

            },
            reduceGoods(gid, price) {
                reducecart('/api/reduce', {gid, price}).then(res => {
                    let data = res.data;

                    if (data.code == SUCCESS) {

                        //ä¿®æ¹vuex cart
                        this.$store.commit('delGoods', {gid, price});
                        this.$store.commit('calcTotal');
                        this.$store.commit('calcPrice');


                    }
                });
            }


        },
        beforeMount() {
            this.getCate();
        },
        mounted() {

        },

        watch: {
            currentCate() {
                this.current = 0;
                this.total = 0;
                this.goods = [];
                // this.finished = false;
                this.onLoad();


            }
        },
        components: {
            MyFooter
        }
    }
</script>

