<script>
    import Vue from 'vue';
    import {Toast} from 'vant';

    Vue.use(Toast);
    import {SUCCESS, IMGHOSTNAME} from "../../config/base";
    import MyFooter from '../../components/my-footer/my-footer'
    import {mapState, mapGetters} from 'vuex'
    import {addcart, togglegoodsstatus, insertorders,reducecart} from "../../api/cart";

    export default {
        name: "indexcar",
        data() {
            return {
                imgHostName: IMGHOSTNAME,
            }
        },
        methods: {
            addGoods(goods) {
                let {gid, mprice, gname, gthumb} = goods;
                addcart('/api/cart', {gid: gid, price: mprice}).then(res => {
                    let data = res.data;

                    if (data.code == SUCCESS) {
                        let {uid, cid} = data.data;
                        //ä¿®æ¹vuex cart
                        this.$store.commit('incGoods', {cid, gid, uid, mprice, gname, gthumb});
                        this.$store.commit('calcTotal');
                        this.$store.commit('calcPrice');

                    }
                });


            },
            reduceGoods(gid, price) {
                reducecart('/api/reduce', {gid, price}).then(res => {
                    let data = res.data;

                    if (data.code == SUCCESS) {

                        //ä¿®æ¹vuex cart
                        this.$store.commit('delGoods', {gid, price});
                        this.$store.commit('calcTotal');
                        this.$store.commit('calcPrice');


                    }
                });
            },
            toggleStatus(goods) {
                let {gid, mprice} = goods;
                togglegoodsstatus('/api/cart/12', {gid, price: mprice}).then(res => {
                    let data = res.data;
                    if (data.code == SUCCESS) {

                        this.$store.commit('togglegoodsstatus', {gid});
                        this.$store.commit('calcTotal');
                        this.$store.commit('calcPrice');
                    }

                })
            },
            Settlement() {
                if (!this.existSelectGoods) {
                    Toast.fail('è¯·è³å°éæ©ä¸ä»¶åå');
                    return;
                } else {
                    insertorders('/api/orders',).then(res => {
                        let data = res.data;
                        if (data.code == SUCCESS) {
                            this.$store.commit('ordersFinish');
                            this.$router.push({name: 'orders', query: {ordersnum: data.ordersnum}})


                        } else {
                            Toast.fail('ä¸åå¤±è´¥');
                        }

                    })

                }

            }

        },
        components: {
            MyFooter
        },
        computed: {
            ...mapState({
                cart: 'cart',
            }),
            ...mapGetters({
                isfullSelected: 'isfullSelected',
                existSelectGoods: 'existSelectGoods',
            })
        }
    }
</script>

