<template>
    <div>
        <!-- å¤´é¨å¯¼èªæ å¼å§ -->
        <ListHeader>
            {{cate.cname}}
        </ListHeader>
        <div class="kong">

        </div>

        <van-pull-refresh v-model="isLoading" @refresh="getcategory">


            <!--è½®æ­å¾å¼å§-->
            <div class="banner">
                <img :src="imghostname+cate.thumb" :alt="cate.cname" style="width: 100%; height: 149px;">

            </div>

            <!--è½®æ­å¾ç»æ-->

            <!--ä¸»ä½é¨åå¼å§-->
            <div class="cp-box" v-for="(v,index) in goods" :key="'goodss'+index">
                <div class="cp-box-left">

                    <router-link :to="{name:'shopdetail',query:{id:v.gid}}">

                        <img :src="imghostname+v.gthumb" :alt="v.gname">

                    </router-link>


                </div>
                <div class="cp-box-right">
                    <div class="cp-box-right-top">
                        <span>{{v.gname}}</span>
                    </div>
                    <div class="cp-box-right-center">
                        ï¿¥{{v.mprice}}
                        <i>ï¿¥{{v.sprice}}</i>

                    </div>
                    <button class="btn">ç«å³è´­ä¹°</button>
                </div>
            </div>


            <!--ä¸»ä½é¨åç»æ-->


        </van-pull-refresh>


    </div>
</template>

<script>
    import ListHeader from '../../components/my-header/list-header'
    import {api} from "../../config/api";
    import {IMGHOSTNAME} from "../../config/base";

    export default {
        name: "shoplist",
        components: {
            ListHeader
        },
        data() {
            return {
                imghostname: IMGHOSTNAME,
                categoods: [],
                cate: [],
                cname: [],
                goods: [],
                isLoading: false,
                limit: 1,
                current: 0,
                total: 0,
            }
        },
        methods: {

            //è·åæ°æ®ï¼æ¸²æé¡µé¢
            getGoods(id,) {
                api('GET', '/api/catelist/' + id,).then(res => {
                    this.categoods = res.data.goods;
                    this.cname = res.data.cname;
                    this.cate = res.data;
                })

            },
            //ä¸æå·æ°é¡µé¢
            getcategory() {
                this.isLoading = true;

                this.current++;
                if (this.current != 1 && this.current > this.total) {
                    this.isLoading = false;
                    return
                }
                api('GET', '/api/cate', {
                    'cid': this.id,
                    'limit': this.limit,
                    'page': this.current,
                }).then(res => {
                    let total = res.total;
                    let goods = res.data;
                    this.$toast('å·æ°æå');
                    !this.total && (this.total = Math.ceil(total / this.limit));

                    this.goods = this.goods.concat(goods);
                    this.$nextTick(() => {
                        this.isLoading = false;
                    });
                })

            },

        },

        beforeMount() {
            this.id = this.$route.query.id;
            this.getGoods(this.id,);
            this.getcategory();
        },
    }
</script>

<style scoped>

    /*.kong {*/
    /*width: 100%;*/
    /*height: 0.88rem;*/
    /*}*/
    .kong {
        width: 100%;
        height: 0;
    }

    .hd {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 0.88rem;
        line-height: 0.88rem;
        font-size: 0.32rem;
        text-align: center;
        z-index: 666;
        background-color: #fff;
        border-bottom: 1px solid #F6F6F4;
    }

    .hd-left {
        position: absolute;
        left: 0;
        top: 0;
        width: 0.8rem;
        height: 0.88rem;
    }


    .cp-box {
        position: relative;
        width: 94%;
        margin: 0.2rem 3% 0px 3%;
        float: left;
        height: 2.8rem;
        border: 1px solid #DEDEDE;
        display: block;
    }

    .cp-box-left {
        width: 2.8rem;
        height: 2.8rem;
        float: left;
    }

    .cp-box-left img {
        width: 2rem;
        height: 2rem;
        display: block;
        margin: 0.4rem auto;
    }

    .cp-box-right {
        width: calc(100% - 2.8rem);
        height: 2.8rem;
        float: left;
    }

    .cp-box-right-top {
        width: 95%;
        float: left;
        height: 0.8rem;
        line-height: 0.4rem;
        font-size: 0.28rem;
        margin-top: 0.4rem;
    }

    .cp-box-right-center {
        width: 95%;
        float: left;
        margin-top: 0.1rem;
        font-size: 0.32rem;
        color: #EF7D00;
        height: 0.4rem;
    }

    .cp-box-right-center i {
        margin-left: 0.1rem;
        font-size: 0.28rem;
        color: #8D8D8D;
        text-decoration: line-through;
    }

    .btn {
        padding: 0 0.3rem;
        float: left;
        height: 0.56rem;
        line-height: 0.56rem;
        border: none;
        outline: none;
        background: #F47C02;
        color: #fff;
        border-radius: 0.3rem;
        font-size: 0.28rem;
        margin-top: 0.16rem;
        margin-left: 0.2rem;
    }
</style>