import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

import createPersistedState from "vuex-persistedstate"
import {getcart} from "../api/cart";
import {SUCCESS} from "../config/base";
//state æ°æ®çå¯ä¸æ¥æº
//ä¿®æ¹æ°æ®mutations
let store = new Vuex.Store({
    state: {
        token: '',
        isShow: false,
        cart: null,
    },
    getters: {
        //æ¿å°ååçæ°æ®
        goodsNumber: function (state) {
            return function (gid) {
                let num = 0;
                //å¤æ­ææ²¡æè´­ç©è½¦
                if (!state.cart) {
                    return 0;
                }
                let goods = state.cart.goods.filter(ele => ele.gid == gid)[0];

                if (goods) {
                    num = goods.num
                }
                return num
            }
        },

        //è´­ç©è½¦çæ»é
        goodstotal(state) {
            if (!state.cart) {
                return;
            }
            return state.cart.total
        },

        //è´­ç©è½¦çä»·æ ¼
        cartprice(state) {
            if (!state.cart) {
                return;
            }
            return state.cart.price
        },

        //ååæ¯å¦å¨é
        isfullSelected(state) {
            return state.cart && state.cart.goods.every(ele => ele.status);
        },

        existSelectGoods(state) {
            let flag = false;
            if (state.cart) {
                flag = state.cart.goods.some(ele => ele.status);
            }
            return flag;
        },
    },
    mutations: {
        changeToken(state, token) {
            state.token = token
        },
        toggleIsShow(state) {
            state.isShow = !state.isShow
        },
        setcart(state, cart) {
            state.cart = cart
        },

        //ååçæ·»å å°è´­ç©è½¦   è®¾ç½®è´­ç©è½¦
        incGoods(state, payload) {

            let {cid, gid, uid, mprice, gname, gthumb} = payload;
            //å¤æ­æ¯å¦æè´­ç©è½¦
            if (!state.cart) {
                //åå§åè´­ç©è½¦
                state.cart = {
                    cid,
                    uid,
                    price: mprice,
                    total: 1,
                    goods: []
                }
            }
            //å¤æ­å½åååæ¯å¦å­å¨
            let goods = state.cart.goods.filter(ele => ele.gid == gid)[0];

            if (goods) {
                goods.num++;
            } else {
                state.cart.goods.push({gid: gid, num: 1, status: 1, gname, gthumb, mprice})

            }

        },


        //ååçåå°
        delGoods(state, payload) {
            let {gid, price} = payload;

            let goods = state.cart.goods.filter(ele => ele.gid == gid)[0];

            if (goods) {
                goods.num--;
                state.cart.total--;
                state.cart.price = state.cart.price - price;
            }

        },


        //è®¡ç®æ»æ°
        calcTotal(state) {
            let total = 0;
            let goods = state.cart.goods.filter(ele => ele.status);
            if (goods.length) {
                total = goods.reduce((prev, current) => {
                    return prev + current.num
                }, 0)
            }
            state.cart.total = total;
        },


        //è®¡ç®æ»ä»·
        calcPrice(state) {
            let price = 0;
            let goods = state.cart.goods.filter(ele => ele.status);
            if (goods.length) {
                price = goods.reduce(function (prev, current) {
                    return prev + current.num * current.mprice
                }, 0)
            }
            state.cart.price = price;
        },
        togglegoodsstatus(state, payload) {
            let gid = payload.gid;
            let goods = state.cart.goods.filter(ele => ele.gid == gid)[0];
            goods.status = goods.status ? 0 : 1;

        },

        //ä¸åæåæ¸ç©ºè´­ç©è½¦
        ordersFinish(state) {
            state.cart.goods = state.cart.goods.filter(ele => !ele.status);
            if (state.cart.goods.length) {
                state.cart.total = 0;
                state.cart.price = 0;
            } else {
                state.cart = null;
            }
        },
    },
    actions: {
        setcart(content) {
            getcart('api/cart/12').then(res => {
                let data = res.data;
                if (data.code == SUCCESS && data.data) {
                    content.commit('setcart', data.data)
                }
            })
        },


    },
    plugins: [createPersistedState()],



});

export default store;
