/**
 * å°è£fetch
 * paramsï¼
 *      methodï¼è¯·æ±æ¹å¼     string    å¿å¡«
 *                  GET     POST    PUT     DELETE
 *      urlï¼å°å  string      å¿å¡«
 *      dataï¼æ°æ®     å¯¹è±¡      éå¿å¡«
 *      Object.keys{{}}         å¯ä»¥è·åå°å¯¹è±¡çå±æ§
 *
 *      let token = sessionStorage.setItem('token');
 *      1.api(get,'')
 *      2.api(get,'',{})
 *      3.api(get,'api/cate',{page:10,limit:10})        api/cate/page=10&limit=10
 *
 *
 */
import {SUCCESS,HOSTNAME} from "./base";

function api(method,url,data){
    //è½¬ä¸ºå¤§å å»ç©ºæ ¼
    method = method.toUpperCase().trim();


    let headers = {
        token:sessionStorage.getItem('token')
    };
    let option = {
        method,
        headers
    };
    //datas
    //GET  DELETE  =>headers
    if (method ==='GET' && data && Object.keys(data).length){
        let str = '?';
        for (let i in data){
            str += `${i}=${data[i]}&`;
        }
        str = str.slice(0,-1);
        url += str;
    }
    url = HOSTNAME + url;
    if (method === 'POST' || method === 'PUT'){
        let body = JSON.stringify(data);
        headers['Content-type'] = 'application/json';
        option.body = body;
    }
    option.headers = headers;



    let promise = new Promise((resolve,reject)=>{
        fetch(url,option).then(res=>res.json())
            .then(data=>{
                let {code} = data;
                if (code == SUCCESS){
                    resolve(data);
                } else {
                    reject(data);
                }
            })
    });
    return promise;
}
export {api};